	module.exports = {"data":{"markdownRemark":{"html":"<p>JumpStart Georgia requested data about the national Georgian budget from the Georgian government in 2016. We received spreadsheets and PDFs containing valuable, but disconnected, financial data.</p>\n<p>As the sole developer on this project, I was challenged to organize the financial data and create a website visualizing the data. I chose to separate the frontend, for which I used React/Redux, from the backend, which uses Rails.</p>\n<p>If you're interested in contributing, here are the relevant repositories:</p>\n<ul>\n<li><a href=\"https://github.com/JumpStartGeorgia/Georgian-Budget-API\">Backend/API</a></li>\n<li><a href=\"https://github.com/JumpStartGeorgia/Georgian-Budget-Public\">Public Site</a></li>\n<li><a href=\"https://github.com/JumpStartGeorgia/Georgian-Budget-Files\">Files</a></li>\n</ul>","frontmatter":{"slug":"georgian_budget","title":"Georgian Budget","date":"January 1, 2017","tags":"open-source website jumpstart-georgia ruby rails javascript react","mainUrl":"https://biujeti.jumpstart.ge","githubUrl":"https://github.com/JumpStartGeorgia/Georgian-Budget-Public","featuredImage":{"childImageSharp":{"responsiveSizes":{"src":"/static/f361d6f7bbe25f8c698a06313350b3d4-30e0b.png","srcSet":"/static/f361d6f7bbe25f8c698a06313350b3d4-665be.png 360w,\n/static/f361d6f7bbe25f8c698a06313350b3d4-431d3.png 720w,\n/static/f361d6f7bbe25f8c698a06313350b3d4-30e0b.png 1440w","sizes":"(max-width: 1440px) 100vw, 1440px"}}}}}},"pathContext":{"slug":"georgian_budget"}}

